/* eslint-disable react/jsx-no-constructed-context-values */
/* eslint-disable react/prop-types */
import React, {
    createContext,
    forwardRef,
    useContext,
    useImperativeHandle,
    useMemo,
    useReducer
} from 'react';

const RegisteredItemsContext = createContext();

function registeredItemsReducer(state, action) {
    switch (action.type) {
        case 'register': {
            const { items } = state;
            const { id, value, groupId } = action.payload;
            // don't re-add duplicates
            if (items.some(i => i.id === id)) return { items };
            items.push({ id, value, groupId });
            return { items };
        }
        case 'unregister': {
            const { items } = state;
            return { items: items.filter(i => i.id !== action.payload) };
        }
        default: {
            throw new Error(`Unhandled action type: ${action.type}`);
        }
    }
}

const getItemsByGroup = (items, groupId) =>
    items.filter(i => i.groupId === groupId);

const RegisteredItemsProvider = forwardRef((props, externalRef) => {
    const { children, useContentMaxWidth } = props;

    const [state, dispatch] = useReducer(registeredItemsReducer, {
        items: []
    });

    useImperativeHandle(externalRef, () => state, [state]);

    const contextValue = useMemo(
        () => ({
            items: state.items,
            getItemsByGroup,
            register: (id, value, groupId) =>
                dispatch({ type: 'register', payload: { id, value, groupId } }),
            unregister: id => dispatch({ type: 'unregister', payload: { id } }),
            useContentMaxWidth
        }),
        [state.items, useContentMaxWidth]
    );
    return (
        <RegisteredItemsContext.Provider value={contextValue}>
            {children}
        </RegisteredItemsContext.Provider>
    );
});

function useRegisteredItems() {
    // return an empty context if no context is found (ie
    // component is used outside of Footer)
    return (
        useContext(RegisteredItemsContext) || {
            items: [],
            getItemsByGroup: () => [],
            register: () => {},
            unregister: () => {}
        }
    );
}

export { RegisteredItemsProvider, useRegisteredItems };
