import clsx from 'clsx';
import React, { useEffect, useState } from 'react';
import { ConditionalWrapper } from '@element/react-common';
import FooterBottomGroup from './FooterBottomGroup';
import {
    FooterBottomPropTypes,
    FooterBottomDefaultProps
} from './generated/propTypes';
import { useRegisteredItems } from './RegisteredItemsContext';
import FooterBottomWrapper from './FooterBottomWrapper';

const createWrappedContent = (Wrapper, themeColor) => children =>
    <Wrapper themeColor={themeColor}>{children}</Wrapper>;

const FooterBottom = props => {
    const {
        children,
        className,
        copyrightContent,
        footerIcons,
        lgCols,
        mdCols,
        smCols,
        links,
        style,
        themeColor,
        ...customProps
    } = props;

    const classes = clsx(
        'lmnt-footer__bottom',
        themeColor && `lmnt-theme-${themeColor}-bg`,
        className
    );

    const { items, getItemsByGroup, useContentMaxWidth } = useRegisteredItems();
    const [customStyle, setCustomStyle] = useState({ ...style });

    useEffect(() => {
        const count = getItemsByGroup(items, 'footer-bottom');
        setCustomStyle({
            ...style,
            '--lmnt-footer__bottom-cols-lg': lgCols || count,
            '--lmnt-footer__bottom-cols-md': mdCols || count,
            '--lmnt-footer__bottom-cols-sm': smCols || 1
        });
        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [items.length, lgCols, mdCols, smCols, style]);

    return (
        <ConditionalWrapper
            condition={useContentMaxWidth}
            wrapper={createWrappedContent(FooterBottomWrapper, themeColor)}
        >
            <footer className={classes} {...customProps} style={customStyle}>
                {links && (
                    <FooterBottomGroup alignment='start'>
                        {links}
                    </FooterBottomGroup>
                )}
                {copyrightContent && (
                    <FooterBottomGroup>{copyrightContent}</FooterBottomGroup>
                )}
                {footerIcons && (
                    <FooterBottomGroup alignment='end' gap='dense'>
                        {footerIcons}
                    </FooterBottomGroup>
                )}
                {children}
            </footer>
        </ConditionalWrapper>
    );
};

FooterBottom.propTypes = FooterBottomPropTypes;
FooterBottom.defaultProps = FooterBottomDefaultProps;
FooterBottom.displayName = 'FooterBottom';

export default FooterBottom;
