import clsx from 'clsx';
import React from 'react';
import { TypoSubtitle } from '@element/react-typography';
import FooterCol from './FooterCol';
import {
    FooterListPropTypes,
    FooterListDefaultProps
} from './generated/propTypes';

const FooterList = props => {
    const {
        children,
        className,
        title,
        lgColSpan,
        mdColSpan,
        smColSpan,
        ...customProps
    } = props;

    return (
        <FooterCol
            tag='dl'
            title={title}
            className={clsx('lmnt-footer__list', className)}
            {...customProps}
        >
            {title && (
                <TypoSubtitle
                    tag='dt'
                    className='lmnt-footer__list-title'
                    smColSpan={smColSpan}
                    mdColSpan={mdColSpan}
                    lgColSpan={lgColSpan}
                    bold
                >
                    {title}
                </TypoSubtitle>
            )}
            {children}
        </FooterCol>
    );
};

FooterList.propTypes = FooterListPropTypes;
FooterList.defaultProps = FooterListDefaultProps;
FooterList.displayName = 'FooterList';

export default FooterList;
