import React, { forwardRef, useImperativeHandle, useRef } from 'react';
import clsx from 'clsx';
import {
    FooterBottomWrapperPropTypes,
    FooterBottomWrapperDefaultProps
} from './generated/propTypes';

const FooterBottomWrapper = forwardRef((props, externalRef) => {
    const { children, themeColor, className, ...customProps } = props;
    const ref = useRef();
    useImperativeHandle(
        externalRef,
        () => ({
            current: ref.current
        }),
        []
    );
    return (
        <div
            ref={ref}
            className={clsx(
                // TODO: remove typo `lmnt-footer_bottom-wrapper` in future version
                'lmnt-footer_bottom-wrapper',
                'lmnt-footer__bottom-wrapper',
                themeColor && `lmnt-theme-${themeColor}-bg`,
                className
            )}
            {...customProps}
        >
            {children}
        </div>
    );
});

FooterBottomWrapper.displayName = 'FooterBottomWrapper';
FooterBottomWrapper.propTypes = FooterBottomWrapperPropTypes;
FooterBottomWrapper.defaultProps = FooterBottomWrapperDefaultProps;

export default FooterBottomWrapper;
