import clsx from 'clsx';
import React from 'react';
import { Icon } from '@element/react-icon';
import {
    FooterLogoPropTypes,
    FooterLogoDefaultProps
} from './generated/propTypes';

const FooterLogo = props => {
    const { logo, className, ...customProps } = props;

    const classes = clsx('lmnt', 'lmnt-footer__logo', className);

    return (
        <div className={classes} {...customProps}>
            <Icon icon={logo} />
        </div>
    );
};

FooterLogo.propTypes = FooterLogoPropTypes;
FooterLogo.defaultProps = FooterLogoDefaultProps;
FooterLogo.displayName = 'FooterLogo';

export default FooterLogo;
