import clsx from 'clsx';
import React from 'react';
import { TypoSubtitle } from '@element/react-typography';
import {
    FooterBottomContentPropTypes,
    FooterBottomContentDefaultProps
} from './generated/propTypes';

const FooterBottomContent = props => {
    const { children, className, ...customProps } = props;

    const classes = clsx('lmnt', 'lmnt-footer__bottom-content', className);

    return (
        <TypoSubtitle className={classes} level={1} tag='p' {...customProps}>
            {children}
        </TypoSubtitle>
    );
};

FooterBottomContent.propTypes = FooterBottomContentPropTypes;
FooterBottomContent.defaultProps = FooterBottomContentDefaultProps;
FooterBottomContent.displayName = 'FooterBottomContent';

export default FooterBottomContent;
