import clsx from 'clsx';
import React from 'react';
import { Icon } from '@element/react-icon';
import {
    FooterIconPropTypes,
    FooterIconDefaultProps
} from './generated/propTypes';

const FooterIcon = props => {
    const { icon, className, themeColor, ...customProps } = props;

    const iconClasses = clsx('material-icons lmnt lmnt-icon--fill', className);

    return (
        <Icon
            {...customProps}
            className={iconClasses}
            icon={icon}
            variant={themeColor || 'gray'}
            iconType='filled'
        />
    );
};

FooterIcon.propTypes = FooterIconPropTypes;
FooterIcon.defaultProps = FooterIconDefaultProps;
FooterIcon.displayName = 'FooterIcon';

export default FooterIcon;
