import { edsRandomId } from '@element/shared';
import clsx from 'clsx';
import React, { useEffect, useState } from 'react';
import {
    FooterBottomGroupDefaultProps,
    FooterBottomGroupPropTypes
} from './generated/propTypes';
import { useRegisteredItems } from './RegisteredItemsContext';

const FooterBottomGroup = props => {
    // eslint-disable-next-line react/prop-types
    const {
        alignment,
        children,
        className,
        gap,
        id,
        lgColSpan,
        mdColSpan,
        smColSpan,
        margin,
        ...customProps
    } = props;

    const [idActual] = useState(id || edsRandomId());
    const { register } = useRegisteredItems();

    useEffect(() => {
        // run this only once
        register(idActual, null, 'footer-bottom');
        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, []);

    const classes = clsx(
        'lmnt-footer__bottom-group',
        alignment === 'start' && 'lmnt-footer__bottom-group--start',
        alignment === 'end' && 'lmnt-footer__bottom-group--end',
        gap && `lmnt-footer__bottom-group--gap-${gap}`,
        smColSpan && `lmnt-footer__column--sm-span-${smColSpan}`,
        mdColSpan && `lmnt-footer__column--md-span-${mdColSpan}`,
        lgColSpan && `lmnt-footer__column--lg-span-${lgColSpan}`,
        margin && `lmnt-footer__bottom-group--margin-${margin}`,
        className
    );

    return (
        <div id={idActual} className={classes} {...customProps}>
            {children}
        </div>
    );
};

FooterBottomGroup.displayName = 'FooterBottomGroup';
FooterBottomGroup.propTypes = FooterBottomGroupPropTypes;
FooterBottomGroup.defaultProps = FooterBottomGroupDefaultProps;

export default FooterBottomGroup;
