import clsx from 'clsx';
import React from 'react';
import { FooterPropTypes, FooterDefaultProps } from './generated/propTypes';
import { RegisteredItemsProvider } from './RegisteredItemsContext';

const Footer = props => {
    const {
        children,
        className,
        useContentMaxWidth,
        themeColor,
        ...customProps
    } = props;

    const classes = clsx(
        'lmnt lmnt-footer',
        themeColor && `lmnt-theme-${themeColor}-bg`,
        useContentMaxWidth && 'lmnt-footer-with-max-content-width',
        className
    );

    return (
        <RegisteredItemsProvider useContentMaxWidth={useContentMaxWidth}>
            <div className={classes} {...customProps}>
                {children}
            </div>
        </RegisteredItemsProvider>
    );
};

Footer.propTypes = FooterPropTypes;
Footer.defaultProps = FooterDefaultProps;
Footer.displayName = 'Footer';

export default Footer;
