/* eslint-disable react/prop-types */
import clsx from 'clsx';
import React, { useEffect, useState } from 'react';
import { TypoSubtitle } from '@element/react-typography';
import { edsRandomId } from '@element/shared';
import { useRegisteredItems } from './RegisteredItemsContext';
import {
    FooterColDefaultProps,
    FooterColPropTypes
} from './generated/propTypes';

const FooterCol = props => {
    const {
        children,
        className,
        id,
        lgColSpan,
        mdColSpan,
        smColSpan,
        title,
        groupId,
        titleSize,
        tag: Tag = 'div',
        ...customProps
    } = props;

    const [idActual] = useState(id || edsRandomId());
    const { register } = useRegisteredItems();

    useEffect(() => {
        // run this only once
        register(idActual, null, 'footer-content');
        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, []);

    return (
        <Tag
            {...customProps}
            id={id}
            className={clsx(
                'lmnt-footer__column',
                smColSpan && `lmnt-footer__column--sm-span-${smColSpan}`,
                mdColSpan && `lmnt-footer__column--md-span-${mdColSpan}`,
                lgColSpan && `lmnt-footer__column--lg-span-${lgColSpan}`,
                className
            )}
        >
            {title && (
                <TypoSubtitle
                    tag='dt'
                    className={clsx('lmnt-footer__list-title', className)}
                    bold
                >
                    {title}
                </TypoSubtitle>
            )}
            {children}
        </Tag>
    );
};

FooterCol.displayName = 'FooterCol';
FooterCol.propTypes = FooterColPropTypes;
FooterCol.defaultProps = FooterColDefaultProps;

export default FooterCol;
