/* eslint-disable arrow-body-style */
import clsx from 'clsx';
import React, { useEffect, useState } from 'react';
import { useRegisteredItems } from './RegisteredItemsContext';
import {
    FooterContentPropTypes,
    FooterContentDefaultProps
} from './generated/propTypes';

const FooterContent = props => {
    const {
        children,
        className,
        gap,
        themeColor,
        smCols,
        mdCols,
        lgCols,
        style,
        ...customProps
    } = props;

    const { items, getItemsByGroup } = useRegisteredItems();
    const [customStyle, setCustomStyle] = useState({ ...style });

    useEffect(() => {
        const count = getItemsByGroup(items, 'footer-content').length;

        setCustomStyle({
            ...style,
            gap,
            '--lmnt-footer__cols-lg': lgCols || count,
            '--lmnt-footer__cols-md': mdCols || count,
            '--lmnt-footer__cols-sm': smCols || 1
        });
        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [items.length, lgCols, mdCols, smCols, style]);

    return (
        <div className='lmnt-footer__content-wrapper'>
            <div
                className={clsx(
                    'lmnt-footer__content',
                    themeColor && `lmnt-theme-${themeColor}-bg`,
                    className
                )}
                {...customProps}
                style={customStyle}
            >
                {children}
            </div>
        </div>
    );
};

FooterContent.propTypes = FooterContentPropTypes;
FooterContent.defaultProps = FooterContentDefaultProps;
FooterContent.displayName = 'FooterContent';

export default FooterContent;
